*{
  box-sizing: border-box;
}

body, html, h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
}

body{
  background-image: url("https://images.pexels.com/photos/1843717/pexels-photo-1843717.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
  background-attachment: fixed;
  background-size: cover;
  font-family: sans-serif;
}

i {
  cursor: pointer;
}

header{
  width: 100%;
  max-width: 500px;
  margin: 1em auto 2em auto;
  padding: 20px;
  background-color: steelblue;
  color: white;
  display: flex;
  justify-content: space-around;
  border-radius: 13px;
  align-items: center;
}

.logout{
  border-radius: 13px;
  border: 2px white solid;
  background-color: steelblue;
  color: white;
  padding: 5px;
  cursor: pointer;
}

.logout:hover{
  /* border: 2px black solid; */
  background-color: white;
  color: steelblue;
  -webkit-transform: scale(1.1);
          transform: scale(1.1)
}

.main{
  max-width: 500px;
  background-color: steelblue;
  color: white;
  padding: 20px;
  margin: auto;
  border-radius: 13px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  -webkit-transition: all, .25s;
  transition: all, .25s;
}

.mainGrid{
  width: 100%;
  -webkit-transition: all, .25s;
  transition: all, .25s;

}




.taskInput{
  width: 100%;
  display: flex;
  margin-bottom: 1em;
  align-items: center;
  justify-content: space-between;
}

.taskInput input{
  width: 90%;
  padding: 5px;
  font-size: 16px;
  border: none;
  background-color: steelblue;
  color: white;
  border-bottom: 1px white solid;
}

.taskInput input:focus{
  outline: none;
}

::-webkit-input-placeholder { 
  color: white;
  opacity: 1; 
}

::-moz-placeholder { 
  color: white;
  opacity: 1; 
}

::-ms-input-placeholder { 
  color: white;
  opacity: 1; 
}

::placeholder { 
  color: white;
  opacity: 1; 
}

.taskInput i{
  color: white;
}
.task{
  background-color: white;
  color: steelblue;
  padding: 10px;
  display: grid;
  justify-content: space-between;
  grid-template-columns: 5fr 1fr;
  margin-bottom: 1em;
  min-height: 60px;
  border-radius: 13px;
  align-items: center;
  box-shadow: 5px 5px 8px black;
  -webkit-transition: all, .25s;
  transition: all, .25s;
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
  -webkit-animation-name: loadAnimation;
          animation-name: loadAnimation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes loadAnimation{
  0% {opacity:0; -webkit-transform:scale(0); transform:scale(0)}
  100% {opacity: 1; -webkit-transform: scale(1); transform: scale(1)}
}

@keyframes loadAnimation{
  0% {opacity:0; -webkit-transform:scale(0); transform:scale(0)}
  100% {opacity: 1; -webkit-transform: scale(1); transform: scale(1)}
}

.task:hover{
  box-shadow: none;
  -webkit-transition: all, .25s;
  transition: all, .25s;
}

.taskLeave{
  position: relative;
  -webkit-animation-name: leave;
          animation-name: leave;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes leave {
  0% {opacity:1; -webkit-transform:scale(1); transform:scale(1)}
  100% {opacity: 0; -webkit-transform: scale(0); transform: scale(0)}
}

@keyframes leave {
  0% {opacity:1; -webkit-transform:scale(1); transform:scale(1)}
  100% {opacity: 0; -webkit-transform: scale(0); transform: scale(0)}
}

.taskBtnGroup{
  padding: 3px;
  display: flex;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: auto;
}

.fa-times-circle{
  margin-left: 5px;
  color: red;
}

.comp{
  color: steelblue;
}

.notComp{
  color: green;
}
.loginArea{
  width: 400px;
  margin: 1em auto;
  padding: 15px;
  font-family: sans-serif;
  color: white;
}

.signup{
  background-color: rgb(70, 180, 125);
}

.login{
  background-color: steelblue;
}

.loginGroup{
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.logTitle{
  text-align: center;
}

.loginGroup label{
  font-size: 22px;
}

.loginGroup input {
  border: none;
  border-bottom: 1px white solid;
  color: white;
  font-size: 24px;
  margin: 5px 0;
  padding: 2px;
}

.loginGroup input:focus{
  outline: none;
}

.buttonArea{
  margin-top: 1em;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.logBtn{
  display: block;
  width: 48%;
  padding: 8px;
  font-size: 16px;
  border: none;
  outline: none;
  -webkit-transition: all, .25s;
  transition: all, .25s;
  background-color: white;
}

.logBtn:hover{
  -webkit-transform: scale(.98);
          transform: scale(.98);
  -webkit-transition: all, .25s;
  transition: all, .25s;
}

.sign{
  color: rgb(70, 180, 125)
}

.log{
  color: steelblue;
}




